import { createWebHistory, createRouter } from "vue-router";
import MainContent from './components/MainContent.vue'
import About from './components/about/About.vue'
import Intro from './components/about/Intro.vue'
import History from './components/about/History.vue'
import Org from './components/about/Org.vue'
import Partners from './components/about/Partners.vue'
import Panorama from './components/about/Panorama.vue'
import Location from './components/about/Location.vue'
import Product from './components/product/Product.vue'
import ProcessMold from './components/process/ProcessMold.vue'
const routes = [
  {
    path:'/',
    component: MainContent
  },
  {
    path:'/about',
    component: About,
    children: [
        {
            path:'intro',
            component: Intro,
        },
        {
            path:'history',
            component: History,
        },
        {
            path:'org',
            component: Org,
        },
        {
            path:'partners',
            component: Partners,
        },
        {
            path:'panorama',
            component: Panorama,
        },
        {
            path:'location',
            component: Location,
        },
    ]
  },
  {
    path: '/product/:id',
    component: Product,
  },
  {
    path: '/process/:id',
    component: ProcessMold,
  }
];

const router = createRouter({
  history: createWebHistory(),
  routes,
  scrollBehavior (to, from, savedPosition){ // eslint-disable-line no-unused-vars
    return { top: 0 }
  }
});

export default router; 